@import "./utils";

#quizzModale_innerContainer {
  margin: 20px;
  width: 50%;
  padding: 3rem;
  overflow-y: auto;
  background-image: url("../assets/ladybird-fly-opacity.png");
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  @include responsiveTabloids {
    width: 80%;
  }
  @include responsiveSmartphones {
    width: 90%;
    padding: 3rem 1rem 3rem 1rem;
  }
}

#quizzModale_header {
  left: 3.3rem;
  right: 3.5rem;
  top: 1rem;
  @include responsiveSmartphones {
    left: 1.5rem;
    right: 1.5rem;
  }
}

#quizzModale_title {
  font-size: map-get($sizes, xl);
  color: map-get($colors, grass);
  @include responsiveSmartphones {
    font-size: 20px;
  }
}

#quizzModale_content {
  text-align: left;
}

@keyframes anim-quizzIcon {
  from {
    transform: rotate(45deg);
  }
  to {
    transform: rotate(405deg);
  }
}

#quizzModale_icon {
  display: flex;
  padding: 0.2rem;
  font-size: 17px;
  background-color: map-get($colors, grass);
  transform: rotate(45deg);
  @include responsiveSmartphones {
    font-size: 15px;
  }
  &:hover {
    background-color: map-get($colors, sky);
    animation: anim-quizzIcon 3000ms infinite linear;
  }
  svg {
    transform: rotate(-45deg);
  }
}
